.line {
  width: 100%;
  height: 1px;
  background-color: #e7f2fd;
  margin-top: 55px;
  margin-bottom: 50px;
}

.container {
  margin: 0 auto;
  padding: 20px 15px;
  max-width: var(--max-content-width);
  text-align: center;
}

.block {
  text-align: left;
}

.block a {
  font-weight: 100;
  color: var(--color-gray);
}

.block a:hover {
  color: var(--color-black);
}

.bolder {
  font-weight: bolder;
}

.title {
  margin-bottom: 20px;
}

.section {
  display: block;
}

.section .container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

